/* General Styles */
body {
  margin: 0;
  font-family: Arial, sans-serif;
  background-color: #f0f8ff; /* Light blue background */
}

h1 {
  color: #2a52be; /* Cobalt Blue */
  text-shadow: 2px 2px 5px rgba(0, 0, 0, 0.2);
}

h2 {
  color: #ff6347; /* Tomato Red */
}

input {
  border: 2px solid #2a52be;
  border-radius: 5px;
  outline: none;
  padding: 10px;
  font-size: 16px;
  width: 300px;
}

button {
  background-color: #ff7f50; /* Coral */
  border: none;
  border-radius: 5px;
  color: white;
  font-size: 16px;
  padding: 10px 20px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

button:hover {
  background-color: #ff4500; /* Orange Red */
}

/* User Information Styles */
.user-details {
  margin-top: 20px;
  display: inline-block;
  padding: 20px;
  background-color: #ffffff; /* White background */
  border: 2px solid #2a52be;
  border-radius: 10px;
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1);
  text-align: left;
}

.user-details img {
  border-radius: 50%;
  margin-bottom: 10px;
}

.user-details p {
  font-size: 18px;
  margin: 5px 0;
}

.error-message {
  color: red;
  font-weight: bold;
  margin-top: 20px;
}
